.w-e-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 4vw;
  margin-bottom: 0;
  list-style: none;

  &__item {
    flex-basis: 48%;
    margin: 0 1% 2%;
    padding: 2rem 2.5rem;
    background-color: $bg_1;
    border-radius: .375rem;

    &--size-l {
      flex-basis: 60%;
    }

    &--bg-1 {
      background-image: url('../images/ad_bg_1.jpg');
      background-position: 100% 0;
      background-size: 10rem;
      background-repeat: no-repeat;
    }

    &--bg-2 {
      background-image: url('../images/ad_bg_2.jpg');
      background-position: 100% 0;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }
}

.mobile {
  position: relative;
  flex-basis: 55%;
  border-radius: 50%;
  overflow: hidden;

  &__backdrop {
    display: block;
    width: 100%;
    padding-bottom: 100%;
    background-color: $gray_1;
  }

  &__img {
    position: absolute;
    bottom: 0;
    left: 50%;
    max-width: 20rem;
    margin-left: -10rem;
  }
}

.gs-mob {
  &-wrap {
    opacity: 0;
    transform: scale(0);
  }
}
