
body {
  overflow-x: hidden;
}

.header {
  display: flex;
  width: 100%;
  padding: 2.5rem 0;

  &__logo {
    display: inline-block;
    width: 3.75rem;
    height: 3.75rem;
    text-decoration: none;
  }

  &__nav {
    display: flex;
    align-items: center;
    margin-left: auto;
  }
}

.nav-list {
  display: flex;
  padding-left: 0;
  padding-right: 2rem;
  list-style: none;

  > li {
    padding: .5rem 1.8rem;
    color: $blue_dark;
    font-size: .875rem;
  }
}

.promo {
  position: relative;
  padding: 3rem 0;

  &__title {
    max-width: 28rem;
    margin-top: 0;
    margin-bottom: 3rem;
    font-family: $title-font;
    font-size: 3.5rem;
    font-weight: bold;
    line-height: 1.29;
  }

  &__text {
    max-width: 25rem;
    font-size: 1em;
    line-height: 1.5;
  }

  &__image {
    position: absolute;
    max-width: 50vw;
    top: 0;
    right: -4vw;
    z-index: -1;
  }
}

.banner {
  padding: 3.5rem;
  background-color: $blue;
  background-image: url('../images/banner_l.jpg'), url('../images/banner_r.jpg');
  background-position: 0 50%, 100% 0;
  background-size: contain;
  background-repeat: no-repeat, no-repeat;
  border-radius: 3rem;
  text-align: center;

  &__title {
    max-width: 22rem;
    margin: 0 auto 2rem;
    color: $white;
    font-weight: bold;
    font-size: 2.25rem;
    font-family: $title-font;

    .light {
      font-weight: 400;
    }
  }

  &-down .banner {
    transform: translateY(50%);
  }

  &-up .banner {
    transform: translateY(-50%);
  }
}

.test {
  background-color: #cccccc;
  padding-top: 14vw;
  padding-bottom: 20vw;
}
