.section {
  padding-top: 5vw;
  padding-bottom: 5vw;

  &__title {
    padding-top: 2rem;
    margin-top: 0;
    margin-bottom: 3rem;
    font-size: 2.5rem;
    text-align: center;
    font-family: $title-font;

    &--l {
      font-size: 3.5rem;
    }

    &--accent {
      color: $acccent;
    }
  }

  &__description {
    max-width: 20.25rem;
    margin-left: auto;
    margin-right: auto;
    font-size: 1rem;
    font-weight: normal;
    opacity: .35;
    text-align: center;
  }

  &--bg-g {
    background-color: $gray;
  }
}

.text-block {
  max-width: 24rem;

  &__title {
    margin-top: 1.5rem;
    margin-bottom: 1rem;
    font-size: 1.5rem;
    font-family: $title-font;
  }

  &__description {
    font-size: 1rem;
    opacity: .35;
  }

  &__content {
    font-size: 1rem;
    line-height: 1.5;
  }

  &--fluid {
    max-width: 100%;
  }

  &--s {
    max-width: 20.5rem;
  }

  &--b-bottom {
    border-bottom: 1px solid $gray_1;
  }
}

.workspace {
  padding-top: 5vw;

  &__img-wrapper {
    flex-grow: 1;
    text-align: center;
    opacity: 0;

    > img {
      display: inline-block;
    }
  }
}

.gs-f1-t,
.gs-wsp-t {
  opacity: 0;
}

.gs-f1-t_2,
.gs-f1-t_3,
.gs-f1-t_4 {
  position: absolute;
  opacity: 0;
}

.indicators-group {
  padding-top: 5vw;

  &__img-wrapper {
    position: relative;
    flex-basis: 55%;
    padding-bottom: 55%;
    margin-left: auto;
    background-color: $gray_1;
    overflow: hidden;
    border-radius: 50%;
    transform: scale(0);
    opacity: 0;

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 75%;
      opacity: 0;
    }
  }
}

.gs-tile_1,
.gs-tile_2 {
  li {
    opacity: 0;
  }
}

.tile {
  padding: 2rem 0;

  &__row {
    display: flex;
    list-style: none;
  }
}

.tile-item {
  position: relative;
  min-width: 9.56rem;
  margin: 0 .5rem;
  padding: 1rem;
  background-color: $white;
  border-radius: .4rem;
  font-size: .875rem;

  &--small {
    display: flex;
    min-width: 8.34rem;
    padding: 0;
    overflow: hidden;

    .tile-item__title {
      display: inline-block;
      padding: .5rem;
    }
  }

  &__icon {
    &--top {
      position: absolute;
      top: 0;
      right: 1rem;
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 50%;
      transform: translateY(-50%);
    }

    &--left {
      display: inline-block;
      width: 3.125rem;
      height: 100%;
    }

    &--orange {
      background-color: $orange;
    }

    &--green {
      background-color: $green;
    }
  }

  &__title {
    margin: 0;
    opacity: .4;
    text-transform: uppercase;
  }

  &__value {
    font-weight: bold;
  }

  &__score {
    &--positive {
      color: $green;
    }

    &--negative {
      color: $red;
    }
  }
}
