.section-plans {

}

.plans {
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  padding-top: 6rem;
  list-style: none;

  &__item {
    flex-basis: 33.333%;
    padding: 0 4rem;
    background-color: $white;

    &:nth-child(2) {
      border-right: 1px solid $gray_1;
      border-left: 1px solid $gray_1;
    }
  }
}

.gs-plan-l,
.gs-plan-r {
  //opacity: 0;
}

.plan {
  &__img {
    width: 6rem;
    margin: -3rem auto 3rem;
  }

  &__price {
    display: flex;
    margin-bottom: 1.2rem;

    i {
      align-self: flex-start;
      font-size: 1.125rem;
      font-weight: bold;
      font-style: normal;
    }

    p {
      margin: 0 .3rem;
      padding: 0;
      font-weight: bold;
      font-size: 1.5rem;
    }

    span {
      align-self: flex-end;
      opacity: .32;
      font-size: 1rem;
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 2vw;
    font-size: 1.125rem;
    font-weight: bold;
    text-align: center;
  }

  &__p {
    position: relative;
    font-size: .875rem;

    & + .plan__p {
      margin-top: 1.2rem;
    }

    &::before {
      content: '';
      position: absolute;
      left: -1.5rem;
      top: .2rem;
      width: 1rem;
      height: .5rem;
      border-left: 2px solid $gray_1;
      border-bottom: 2px solid $gray_1;
      transform: rotateZ(-45deg);
    }
  }

  &__link {
    padding-top: 1.2rem;

    a {
      color: $acccent;
      text-decoration: none;
      transition: opacity .2s;

      &:hover {
        opacity: .7;
      }
    }
  }

  &__actions {
    padding: 3vw 0;
    text-align: center;
  }
}

.section-coming-features {
  padding-bottom: 15vw;
}

.coming-features {
  display: flex;

  &__title {
    flex-basis: 50%;

    > * {
      text-align: left;
    }
  }

  &__tags {
    flex-basis: 50%;
    text-align: center;
  }
}

.tag {
  display: inline-block;
  padding: 1.4rem 2.6rem;
  color: $acccent;
  font-weight: bold;
  background-color: $gray_3;
  border-radius: .375rem;

  &--1 {
    &::before {
      bottom: 1.8rem;
      right: -8rem;
      width: 2.5rem;
    }

    &::after {
      bottom: 0;
      right: -7rem;
      width: 5rem;
    }
  }

  &--2 {
    margin-top: 4.5rem;

    &::before {
      top: -2rem;
      left: -3rem;
      width: 2rem;
    }

    &::after {
      top: -2rem;
      left: 0rem;
      width: 5.8rem;
    }
  }

  &--3 {
    position: relative;
    top: -2.5rem;
    margin-left: 2rem;
  }

  &--lines {
    position: relative;

    &::before,
    &::after {
      content: '';
      position: absolute;
      height: 1rem;
      background-color: $gray_3;
      border-radius: .2rem;
    }
  }
}
