.actions-list {
  display: flex;
  padding-left: 0;
  list-style: none;

  li + li {
    border-left: 1px solid $gray_1;
  }

  a {
    padding: 0 .6rem;
    display: inline-block;
    font-size: .875rem;
    color: $blue_dark;
    line-height: 1.72;

    & > span {
      opacity: .4;
    }
  }
}

.footer {
  display: flex;
  justify-content: space-between;

  &__copyright {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .logo {
      display: inline-block;
      width: 3.75rem;
      height: 3.75rem;
    }

    .copyright-msg {
      font-size: .875rem;
      color: $blue_dark;
      opacity: .4;
      line-height: 1.72;

      a {
        color: $blue_dark;
      }
    }
  }

  &__nav {
    font-size: .875rem;

    .title {
      font-weight: bold;
    }
  }

  &__actions {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.footer-nav {
  padding-left: 0;
  list-style: none;
  column-count: 3;
  column-gap: 2rem;

  &__link {
    display: inline-block;
    padding: .5rem;
    color: $base-txt;
  }
}
